p {
	line-height: 1.5;
}

.App {
	background-color: #030303;
	color: #f7f7f7;
	display: flex;
	height: 100vh;
	max-height: 100vh;
	min-height: 100vh;
}

@media screen and (max-width: 768px) {
	.App {
		flex-direction: column;
		height: 100%;
		padding: 0 8px;
	}
}

.Twitch-view {
	padding: 16px;
	flex: 2;
}

.User-interface {
	padding: 16px;
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.Instruction {
	margin-bottom: 16px;
}

.Instruction-title {
	font-size: 2rem;
	margin-bottom: 8px;
}

.Audio-settings {
	margin-bottom: 16px;
}

.Audio-title {
	font-size: 1.75rem;
	margin-bottom: 8px;
}

.Audio-items {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.Audio-1 {
	text-align: center;
	align-items: center;
	justify-content: center;
	flex: 2;
}

.toggle_input {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 5;
	opacity: 0;
	cursor: pointer;
}

.toggle_label {
	width: 75px;
	height: 35px;
	background: #ccc;
	position: relative;
	display: inline-block;
	border-radius: 30px;
	transition: 0.4s;
	box-sizing: border-box;
}

.toggle_label:after {
	content: '';
	position: absolute;
	width: 35px;
	height: 35px;
	border-radius: 100%;
	left: 0;
	top: 0;
	z-index: 2;
	background: #fff;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
	transition: 0.4s;
}

.toggle_input:checked + .toggle_label {
	background-color: #5657f6;
}

.toggle_input:checked + .toggle_label:after {
	left: 40px;
}

.toggle_button {
	position: relative;
	width: 75px;
	height: 35px;
	align-items: left;
	justify-content: left;
}

.record-button {
	width: 100%;
	cursor: pointer;
	background: #5657f6;
	color: #f7f7f7;
	font-size: 1.5rem;
	text-transform: uppercase;
	font-weight: bold;
	padding: 8px;
	border: solid 4px #5657f6;
	border-radius: 24px;
}

.record-button:hover {
	background: none;
	color: #5657f6;
}

.record-button.disabled {
	cursor: not-allowed;
}

.record-button.disabled:hover {
	background: #5657f6;
	color: #f7f7f7;
}
